import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NewsCategoriesPageComponent } from './news-categories-page.component';

const routes: Routes = [
  { path: '', component: NewsCategoriesPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class NewsCategoriesPageRoutingModule { }
