import { Component, OnInit } from '@angular/core';

import { NewCategoryService } from '../../angular-services/new-category.service';
import { NewCategory } from '../../classes/new-category';

@Component({
  selector: 'app-list-news-categories-admin',
  templateUrl: './list-news-categories.component.html',
  styleUrls: ['./list-news-categories.component.css']
})
export class ListNewsCategoriesComponent implements OnInit {

  categories: NewCategory[] = [];
  alert: any = { type: '', message: '', show: false };

  constructor(private newCategoryService: NewCategoryService) { }

  ngOnInit() {
    this.getCategories();
  }

  getCategories() {
    this.newCategoryService.getCategories().subscribe(
      response => {
        if (response !== undefined) {
          this.categories = response;
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao carregar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  removeCategory(category) {
    let confirm = prompt('Para confirmar esta operação digite SIM: \n\n ');
    if (confirm === 'SIM') {
      this.categories = this.categories.filter(c => c !== category);
      this.newCategoryService.removeCategory(category._key).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Categoria excluída com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao carregar os dados!';
            this.alert.show = true;
          }
        }
      )
    } else {
      this.alert.type = 'danger';
      this.alert.message = 'O captcha inválido!';
      this.alert.show = true;
    }
  }

}
